<template>
<div id="now" class="boxSet">
  <h2></h2>
  <div id="container">
    <div id="brief">
      <!--  -->
      <p>{{ propdata.area }}</p>
      <!--  -->
      <p>
        <span id="curTemp" :style="warningTemp(removeUnit(propdata.real))">{{ removeUnit(propdata.real) }}</span>
        &#x2103;
      </p>
      <!-- / -->
      <p>
        <span>{{ propdata.highest }}</span> / <span id="lowTemp">{{ propdata.lowest }}</span>
      </p>
    </div>
    <div id="indexContainer">
      <!--  -->
      <div class="corner">
        <p><span :class="weatherIcon" class="iconfont white"></span></p>
        <p>{{ propdata.weather }}</p>
      </div>
      <!--  -->
      <div class="corner">
        <p class="white">{{ uvIndex(propdata.uv_index) }}</p>
        <p></p>
      </div>
      <!--  -->
      <div class="corner">
        <p class="white">{{aqiIndex(propdata.aqi)}}</p>
        <p></p>
      </div>
    </div>
    <div id="point">
      <!--  -->
      <div v-if="alarm">
        <strong>{{ alarmlevel }}{{ alarmtype }}</strong>
        <p class="indent">{{ alarmcontent }}</p>
      </div>
      <!--  -->
      <template>
        <strong></strong>
        <p class="indent">{{ propdata.tips }}</p>
      </template>
    </div>
  </div>
</div>
</template>

<script>
export default {
  props: {
    parentdata: {
      type: Object,
      default: function () {
        return {}
      }
    }
  },
  data () {
    return {
      propdata: this.parentdata, // 
      alarm: false, // v-ifdiv
      alarmlevel: '', // 
      alarmtype: '', // 
      alarmcontent: '' // 
    }
  },
  watch: {
    parentdata: {
      handler (cur, pre) {
        this.propdata = cur
        // watch
        this.alarms()
      },
      deep: true,
      // 
      immediate: true
    }
  },
  created () {
    this.alarms()
  },
  computed: {
    // 
    removeUnit () {
      return (val) => {
        if (typeof val === 'string') {
          let realTemp = val.split('')
          realTemp.pop()
          realTemp = Number(realTemp.join(''))
          return realTemp
        } else {
          return val
        }
      }
    },
    // 
    warningTemp () {
      return (val) => {
        if (val > 40 | val < -25) {
          return {textShadow: '0 0 0.8rem red'} // 
        } else if (val > 37 | val < -20) {
          return {textShadow: '0 0 0.8rem orange'} // 
        } else if (val > 35 | val < -15) {
          return {textShadow: '0 0 0.8rem yellow'} // 
        } else if (val < -10) {
          return {textShadow: '0 0 0.8rem blue'} // 
        } else {
          return val
        }
      }
    },
    // 
    weatherIcon: function () {
      let weathercode = this.propdata.weatherimg.slice(0, -4)
      return `icon-tianqi-${weathercode}`
    },
    // 
    uvIndex () {
      return (val) => {
        if (val < 3) {
          return ''
        } else if (val < 5) {
          return ''
        } else if (val < 7) {
          return ''
        } else if (val < 10) {
          return ''
        } else {
          return ''
        }
      }
    },
    // 
    aqiIndex () {
      return (val) => {
        if (val < 50) {
          return ''
        } else if (val < 100) {
          return ''
        } else if (val < 150) {
          return ''
        } else if (val < 200) {
          return ''
        } else if (val < 300) {
          return ''
        } else {
          return ''
        }
      }
    }
  },
  methods: {
    // 
    alarms () {
      if (this.propdata.alarmlist.length > 0) {
        this.alarm = true
        this.alarmlevel = this.propdata.alarmlist[0].level
        this.alarmtype = this.propdata.alarmlist[0].type
        this.alarmcontent = this.propdata.alarmlist[0].content
      } else {
        this.alarm = false
      }
    }
  }
}
</script>

<style>

</style>



