<template>
<div class="boxSet" id="wind">
  <h2></h2>
  <div id="windChart"></div>
</div>
</template>

<script>
export default {
  props: {
    parentdata: {
      type: Object,
      default: function () {
        return {}
      }
    }
  },
  data () {
    return {
      propdata: this.parentdata, // 
      // echarts
      windChart: {}, // echarts
      windOption: {}, // echarts
      windradius: '75%', // echarts radar
      optionVal: [1, 1, 1, 1, 1, 1, 1, 1], // echarts8
      optionSpeed: 0, // echarts-optionValwindspeed1123-4
      optionStr: '' // echarts windwindscwindspeed
    }
  },
  watch: {
    parentdata: {
      handler (cur, pre) {
        this.propdata = cur
        // echarts 
        this.optionVal = [1, 1, 1, 1, 1, 1, 1, 1]
        // watch
        this.$nextTick(() => {
          this.windData()
          this.drawRadar()
        })
      },
      deep: true,
      // 
      immediate: true
    }
  },
  created () {
    this.windData()
  },
  mounted () {
    this.drawRadar()
  },
  methods: {
    // 
    windData () {
      // windspeedoptionSpeed
      this.propdata.windspeed = Number(this.propdata.windspeed)
      if (this.propdata.windspeed < 5) {
        this.optionSpeed = 2
      } else if (this.propdata.windspeed < 11) {
        this.optionSpeed = 3
      } else if (this.propdata.windspeed < 19) {
        this.optionSpeed = 4
      } else if (this.propdata.windspeed < 28) {
        this.optionSpeed = 5
      } else if (this.propdata.windspeed < 38) {
        this.optionSpeed = 6
      } else if (this.propdata.windspeed < 49) {
        this.optionSpeed = 7
      } else if (this.propdata.windspeed < 61) {
        this.optionSpeed = 8
      } else if (this.propdata.windspeed < 74) {
        this.optionSpeed = 9
      } else if (this.propdata.windspeed < 88) {
        this.optionSpeed = 10
      } else if (this.propdata.windspeed < 102) {
        this.optionSpeed = 11
      } else {
        this.optionSpeed = 12
      }
      // windoptionStr'...'windoptionSpeedoptionVal[...]
      switch (this.propdata.wind) {
        case '':
          this.optionVal.splice(0, 1, this.optionSpeed)
          // 1-2\n8km/h
          this.optionStr = `<div><p>${this.propdata.wind}${this.propdata.windsc}</p><p>${this.propdata.windspeed}km/h</p></div>`
          break
        case '':
          this.optionVal.splice(1, 1, this.optionSpeed)
          this.optionStr = `<div><p>${this.propdata.wind}${this.propdata.windsc}</p><p>${this.propdata.windspeed}km/h</p></div>`
          break
        case '':
          this.optionVal.splice(2, 1, this.optionSpeed)
          this.optionStr = `<div><p>${this.propdata.wind}${this.propdata.windsc}</p><p>${this.propdata.windspeed}km/h</p></div>`
          break
        case '':
          this.optionVal.splice(3, 1, this.optionSpeed)
          this.optionStr = `<div><p>${this.propdata.wind}${this.propdata.windsc}</p><p>${this.propdata.windspeed}km/h</p></div>`
          break
        case '':
          this.optionVal.splice(4, 1, this.optionSpeed)
          this.optionStr = `<div><p>${this.propdata.wind}${this.propdata.windsc}</p><p>${this.propdata.windspeed}km/h</p></div>`
          break
        case '':
          this.optionVal.splice(5, 1, this.optionSpeed)
          this.optionStr = `<div><p>${this.propdata.wind}${this.propdata.windsc}</p><p>${this.propdata.windspeed}km/h</p></div>`
          break
        case '':
          this.optionVal.splice(6, 1, this.optionSpeed)
          this.optionStr = `<div><p>${this.propdata.wind}${this.propdata.windsc}</p><p>${this.propdata.windspeed}km/h</p></div>`
          break
        default:
          this.optionVal.splice(7, 1, this.optionSpeed)
          this.optionStr = `<div><p>${this.propdata.wind}${this.propdata.windsc}</p><p>${this.propdata.windspeed}km/h</p></div>`
          break
      }
    },
    // 
    drawRadar () {
      this.windChart = this.$echarts.init(document.getElementById('windChart'))
      this.windOption = {
        series: [
          {
            type: 'radar',
            lineStyle: {
              color: 'rgba(239,94,31,1)',
              shadowColor: 'rgba(0,191,255, 0.5)',
              shadowBlur: 20
            },
            symbol: 'none',
            itemStyle: {
              color: 'rgba(239,94,31,1)'
            },
            data: [
              {
                value: this.optionVal, // 8[1, 1, 1, 1, 1, 1, 1, 1]windData
                name: '',
                tooltip: {
                  formatter: this.optionStr
                }
              }
            ]
          }
        ],
        // 
        tooltip: {
          show: true, // 
          trigger: 'item', // 
          confine: true, //  tooltip 
          backgroundColor: 'rgba(255,255,255,0.7)',
          borderColor: 'transparent',
          textStyle: {
            fontWeight: 'bold',
            color: 'black'
          },
          extraCssText: 'width:150px; white-space:pre-wrap',
          formatter: ''
        },
        radar: {
          indicator: [
            { name: '', max: 13 },
            { name: '', max: 13 },
            { name: '', max: 13 },
            { name: '', max: 13 },
            { name: '', max: 13 },
            { name: '', max: 13 },
            { name: '', max: 13 },
            { name: '', max: 13 }
          ],
          // 
          splitNumber: 13,
          splitLine: {
            lineStyle: {
              color: [
                'rgba(0,191,255, 0.1)',
                'rgba(0,191,255, 0.2)',
                'rgba(0,191,255, 0.4)',
                'rgba(0,191,255, 0.6)',
                'rgba(0,191,255, 0.8)',
                'rgba(0,191,255, 1)'
              ]
            }
          },
          splitArea: {
            show: false
          },
          // 
          axisLine: {
            lineStyle: {
              color: 'rgba(0,191,255, 0.5)'
            }
          },
          // 
          axisName: {
            color: 'rgba(145, 95, 35, 1)',
            fontWeight: 'bold',
            fontSize: '0.8rem',
            lineHeight: 0
          },
          radius: this.windradius
        }
      }
      this.windChart.setOption(this.windOption)
    },
    // 
    radarResize () {
      this.windChart.setOption(this.windOption)
      this.windChart.resize()
    }
  }
}
</script>

<style>

</style>



