<template>
  <div class="boxSet" id="daysTemp">
    <h2></h2>
    <div id="daysTempChart"></div>
  </div>
</template>

<script>
export default {
  props: {
    parentdata: {
      type: Object,
      default: function () {
        return {}
      }
    }
  },
  data () {
    return {
      daysWea: this.parentdata.list, // 7
      // echarts
      daysChart: {}, // echarts
      daysOption: {}, // echarts
      dates: [], // 7-x1
      weekDays: [], // 7-x2
      wea: [] // 7-x3
    }
  },
  watch: {
    parentdata: {
      handler (cur, pre) {
        this.daysWea = cur.list
        // watch
        this.$nextTick(() => {
          this.dataSet()
          this.drawDaysTemp()
        })
      },
      deep: true,
      // 
      immediate: true
    }
  },
  created () {
    this.dataSet()
  },
  mounted () {
    this.drawDaysTemp()
  },
  computed: {
    // ,
    removeUnit () {
      // 
      let templist = Array.from(this.daysWea)
      // 
      templist.forEach((params) => {
        // vue
        let lowestTemp = params.lowest
        let highestTemp = params.highest
        // 
        if (typeof lowestTemp === 'string') {
          let backlowest = Array.from(lowestTemp)
          backlowest.pop()
          params.lowest = Number(backlowest.join(''))
        }
        if (typeof highestTemp === 'string') {
          let backhighest = Array.from(highestTemp)
          backhighest.pop()
          params.highest = Number(backhighest.join(''))
        }
      })
      return templist
    }
  },
  methods: {
    dataSet () {
      let tempDate = []
      let temWeek = []
      let tempWea = []
      // foreach.
      let temDays = Array.from(this.daysWea)
      if (temDays !== undefined) {
        temDays.forEach(function (element) {
          tempDate.push(Object.values(element)[0]) // 
          temWeek.push(Object.values(element)[1]) // week
          tempWea.push(Object.values(element)[2]) // 
        })
      }
      this.dayWeathers = temDays
      temDays = null
      this.dates = tempDate // 7
      tempDate = null
      this.weekDays = temWeek // week7week
      temWeek = null
      this.wea = tempWea // 7
      tempWea = null
    },
    drawDaysTemp () {
      // echarts
      this.daysChart = this.$echarts.init(document.getElementById('daysTempChart'))
      // echarts
      this.daysOption = {
        color: ['#d30e06', '#d0dbdb', '#114479'],
        // 
        dataset: {
          dimensions: ['date', 'week', 'weather', 'weatherimg', 'weathercode', 'real', 'lowest', 'highest', 'wind', 'windspeed', 'windsc', 'sunrise', 'sunset', 'moonrise', 'moondown', 'pcpn', 'uv_index', 'vis', 'humidity', 'tips'],
          source: this.removeUnit
        },
        // x
        xAxis: [
          // x:date
          {
            id: 'date',
            type: 'category',
            data: this.dates, // x
            axisLine: {
              show: false, // 
              onZero: false // offset
            },
            offset: 30,
            axisTick: {
              show: false // 
            },
            axisLabel: {
              show: true,
              color: 'rgba(145, 95, 35, 1)',
              fontSize: '0.8rem',
              formatter: function (params) {
                return params.slice(5)
              }
            }
          },
          // x:week
          {
            id: 'week',
            type: 'category',
            data: this.weekDays,
            axisLine: {
              show: false, // 
              onZero: false // offset
            },
            offset: 10,
            position: 'bottom',
            axisTick: {
              show: false // 
            },
            axisLabel: {
              show: true,
              color: 'rgba(145, 95, 35, 1)',
              fontSize: '0.8rem',
              fontWeight: 'bold'
            }
          },
          // x:weather
          {
            id: 'wea',
            type: 'category',
            data: this.wea,
            axisLine: {
              show: false, // 
              onZero: false // offset
            },
            offset: 20,
            axisTick: {
              show: false // 
            },
            axisLabel: {
              show: true,
              color: 'rgba(238,247,242,.8)',
              fontSize: '0.8rem',
              fontWeight: 'bold'
            }
          }
        ],
        series: [
        // 
          {
            name: '',
            type: 'line',
            yAxisIndex: 0,
            smooth: true,
            lineStyle: {
              width: 3
            },
            label: {
              show: true,
              color: 'inherit',
              fontSize: '0.8rem',
              fontWeight: 600,
              formatter: '{@highest}'
            },
            symbolSize: 6,
            encode: {
              x: 'date',
              y: 'highest'
            }
          },
          // 
          {
            name: '',
            type: 'line',
            yAxisIndex: 0,
            smooth: true,
            lineStyle: {
              width: 3
            },
            label: {
              show: true,
              position: 'bottom',
              color: 'inherit',
              fontSize: '0.8rem',
              fontWeight: 600,
              formatter: '{@lowest}'
            },
            symbolSize: 6,
            encode: {
              x: 'date',
              y: 'lowest'
            }
          },
          // 
          {
            name: '',
            type: 'bar',
            yAxisIndex: 1,
            showBackground: true, // 
            backgroundStyle: {
              color: 'rgba(0,191,255,.1)'
            },
            encode: {
              x: 'date',
              y: 'humidity'
            }
          }
        ],
        // 
        tooltip: {
          show: true, // 
          trigger: 'axis', // 
          confine: true, //  tooltip 
          axisPointer: {
            type: 'none'
          },
          backgroundColor: 'rgba(255,255,255,0.7)',
          borderColor: 'transparent',
          textStyle: {
            fontWeight: 'bold',
            color: 'black'
          },
          extraCssText: 'width:120px; white-space:pre-wrap',
          formatter: function (params) {
            let diffDate = new Date() - new Date(params[0].name) // data
            let totalDays = Math.floor(diffDate / (1000 * 60 * 60 * 24)) // 10-1
            if (totalDays === 0) {
              return `<div style="text-align:justify;"><br>${params[0].value.weather}<br>${params[0].value.humidity}%<br>${params[0].value.highest}<br>${params[0].value.lowest}</div>` // htmlstyle
            } else if (totalDays === -1) {
              return `<div style="text-align:justify;"><br>${params[0].value.weather}<br>${params[0].value.humidity}%<br>${params[0].value.highest}<br>${params[0].value.lowest}</div>`
            } else {
              return `<div style="text-align:justify;"><h3 style="margin-top:0;">${params[0].value.date.slice(5)}</h3>${params[0].value.weather}<br>${params[0].value.humidity}%<br>${params[0].value.highest}<br>${params[0].value.lowest}</div>`
            }
          }
        },
        legend: {
          show: false // 
        },
        grid: {
          top: '25%'
        },
        yAxis: [
          {
            type: 'value',
            name: '/C',
            nameTextStyle: {
              align: 'right',
              color: 'rgba(238,247,242,.7)'
            },
            position: 'left',
            // 
            axisLabel: {
              color: function (val) {
                if (val > 40 | val < -25) {
                  return 'red' // 
                } else if (val > 37 | val < -20) {
                  return 'orange' // 
                } else if (val > 35 | val < -15) {
                  return 'yellow' // 
                } else if (val < -10) {
                  return 'blue'// 
                } else {
                  return 'rgba(238,247,242,.7)'
                }
              },
              formatter: '{value} C'
            },
            max: function (value) {
              return value.max
            },
            min: function (value) {
              return value.min - 2
            },
            // y
            splitLine: {
              show: true,
              lineStyle: {
                color: 'rgba(238,247,242,.2)'
              }
            }
          },
          {
            type: 'value',
            name: '/%',
            nameTextStyle: {
              align: 'left',
              color: 'rgba(238,247,242,.7)'
            },
            position: 'right',
            axisLabel: {
              color: 'rgba(238,247,242,.7)',
              formatter: '{value} %'
            },
            max: function (value) {
              return value.max + 2
            },
            min: 0,
            // y
            splitLine: {
              show: false
            }
          }
        ]
      }
      // 
      this.daysChart.setOption(this.daysOption)
    },
    // 
    daysResize () {
      this.daysChart.setOption(this.daysOption)
      this.daysChart.resize()
    }
  }
}
</script>

<style>

</style>



