<template>
<div class="boxSet" id="riseSet">
  <h2></h2>
  <div class="sunChart">
    <!--  -->
    <div>
      <p><span class="iconfont icon-sunrise"></span></p>
      <p>{{ propdata.sunrise }}</p>
    </div>
    <!--  -->
    <div id="sunbar" class="bar" :style="sunbar">
      <div class="barIcon" :style="sunIcon">
        <div class="iconfont icon-090sun"></div>
      </div>
    </div>
    <!--  -->
    <div>
      <p><span class="iconfont icon-sunset"></span></p>
      <p>{{ propdata.sunset }}</p>
    </div>
  </div>
  <!--  -->
  <div class="sunChart">
    <!--  -->
    <div>
      <p><span class="iconfont icon-wi-moonrise"></span></p>
      <p>{{ propdata.moonrise }}</p>
    </div>
    <!--  -->
    <div class="bar" :style="moonbar">
      <div class="barIcon" :style="moonIcon">
        <span class="iconfont icon-moon"></span>
      </div>
    </div>
    <!--  -->
    <div>
      <p><span class="iconfont icon-wi-moonset"></span></p>
      <p>{{ propdata.moondown }}</p>
    </div>
  </div>
</div>
</template>

<script>
export default {
  props: {
    parentdata: {
      type: Object,
      default: function () {
        return {}
      }
    }
  },
  data () {
    return {
      propdata: this.parentdata.list[0], // 
      sunDiff: '', // 
      moonDiff: '' // 
    }
  },
  watch: {
    parentdata: {
      handler (cur, pre) {
        this.propdata = cur.list[0]
        // watch
        this.sunDiff = this.nowTime(this.propdata.sunrise, this.propdata.sunset)
        this.moonDiff = this.nowTime(this.propdata.moonrise, this.propdata.moondown)
      },
      deep: true,
      // 
      immediate: true
    }
  },
  created () {
    this.sunDiff = this.nowTime(this.propdata.sunrise, this.propdata.sunset)
    this.moonDiff = this.nowTime(this.propdata.moonrise, this.propdata.moondown)
  },
  computed: {
    sunbar () {
      let step = this.sunDiff
      return {
        backgroundImage: `linear-gradient(90deg, #fac043 ${step}%,rgba(0,191,255,.1) 1%)`
      }
    },
    // 
    sunIcon () {
      let step = this.sunDiff
      if (step === 98 || step === 0) {
        return {
          marginLeft: `${step}%`,
          animation: 'none'
        }
      } else {
        return {
          marginLeft: `${step}%`,
          animation: 'sunRotate 2s linear infinite'
        }
      }
    },
    // 
    moonbar () {
      let step = this.moonDiff
      return {
        backgroundImage: `linear-gradient(90deg, #1383c2 ${step}%,rgba(0,191,255,.1) 1%)`
      }
    },
    // 
    moonIcon () {
      let step = this.moonDiff
      if (step === 98 || step === 0) {
        return {
          marginLeft: `${step}%`,
          animation: 'none'
        }
      } else {
        return {
          marginLeft: `${step}%`,
          animation: 'rotate 2s linear infinite alternate'
        }
      }
    }
  },
  methods: {
    // 
    nowTime (risetime, settime) {
      let now = new Date()
      let start = new Date('2000-1-1 '.concat(risetime))
      let end = new Date('2000-1-1 '.concat(settime))
      let diff
      // % = -/*100//-/
      if (end.getHours() < start.getHours()) {
        diff = (now.getHours() - start.getHours()) * 100 / (24 - end.getHours() + start.getHours())
      } else {
        diff = (now.getHours() - start.getHours()) * 100 / (end.getHours() - start.getHours())
      }
      // 0
      if (diff < 0) {
        return 0
      } else if (diff > 98) {
        return 98
      } else {
        return diff
      }
    }
  }
}
</script>

<style>
/*  */
.sunChart{
  display: flex;
  flex-flow: row wrap;
  align-items: baseline;
}
/*  */
.bar{
  margin: 20% auto;
  width: 60%;
  height: 10px;
  border-radius: 5px;
  box-shadow: 0 0 1px 2px rgba(1, 32, 123, 1);
}
/*  */
.barIcon{
  float:left;
  box-sizing: border-box;
  margin-top: -2%;
  left: -3%;
  width: 20px;
  height: 20px;
  font-size: 1.25rem;
  transform-origin: center;
  text-align: center;
  position: relative;
}
/*  */
.barIcon > .icon-090sun{
  box-sizing: border-box;
  position: absolute;
  left: -50%;
  top: 50%;
  color: rgba(239,94,31,1);
}
@keyframes sunRotate {
  from{transform: rotate(0);}
  to{transform: rotate(360deg);}
}
/*  */
.barIcon > .icon-moon{
  box-sizing: border-box;
  color: rgba(32,194,241,1);
}
@keyframes rotate {
  from{transform: rotate(15deg);}
  to{transform: rotate(30deg);}
}
</style>



