import axios from 'axios'
import url from './url'

// 
const instance1 = axios.create({
  timeout: 1000
})
// 
const instance7 = axios.create({
  timeout: 1000
})

// 
let backIndex = ''
let backData = []

// 
instance7.interceptors.request.use((config) => {
  let reConfig = cacherules(config) // 
  return reConfig
}, (err) => {
  return Promise.reject(err)
})

// 
instance7.interceptors.response.use((response) => {
  // 2xx 
  // code200
  localStore(response)
  // code200
  if (response.data.code === 200) {
    return response
  } else {
    return Promise.reject(response.data)
  }
}, (error) => {
  //  2xx 
  if (axios.isCancel(error)) {
    console.log('')
    return Promise.resolve(JSON.parse(error.message))
  } else {
    console.log(':', error)
  }
  return Promise.reject(error)
})

// urlurl.jsinstanceparams
const api = {
  // 
  getWeather1 (areaid) {
    return instance1.get(url.tianqi, {
      params: {
        city: areaid,
        type: '1' // 17
      }
    })
  },
  getWeather7 (areaid) {
    return instance7.get(url.tianqi, {
      params: {
        city: areaid,
        type: '7' // 17
      }
    })
  }
}
export default api

//  localStorage storageAvailable
function storageAvailable (type) {
  var storage
  try {
    storage = window[type]
    var x = '__storage_test__'
    storage.setItem(x, x)
    storage.removeItem(x)
    return true
  } catch (e) {
    return e instanceof DOMException && (
    // everything except Firefox
      e.code === 22 ||
          // Firefox
          e.code === 1014 ||
          // test name field too, because code might not be present
          // everything except Firefox
          e.name === 'QuotaExceededError' ||
          // Firefox
          e.name === 'NS_ERROR_DOM_QUOTA_REACHED') &&
          // acknowledge QuotaExceededError only if there's something already stored
          (storage && storage.length !== 0)
  }
}

// localStoreparamsstorageAvailable localStorage 
function localStore (params) {
  if (storageAvailable('localStorage')) {
    // We can use localStorage awesomeness
    let cacheIndex = '' // localStorage{cacheIndex:cacheData}
    let cacheData = [] // [{expiretime:,response:params}]
    let cacheObj = {} // {expiretime:,response:params}
    let expiretime = '' // 

    // code200
    if (params.data.code === 200) {
      console.log('', params)
      cacheIndex = JSON.stringify(params.config.url + params.config.method + JSON.stringify(params.config.params)) // url
      expiretime = Date.now() + 24 * 60 * 60 * 1000 // +24h
      // [{expiretime:,response:params}]
      cacheObj.expiretime = expiretime
      cacheObj.response = params
      cacheData.push(cacheObj)
      // 
      localStorage.setItem(cacheIndex, JSON.stringify(cacheData))
      if (localStorage.getItem(cacheIndex)) {
        console.log('')
      } else {
        console.log('')
      }
    } else {
      console.log('params:', params)
    }
  } else {
    console.log('')
  }
  return params
}

// params
function cacherules (params) {
  // 
  const CancelToken = axios.CancelToken
  const source = CancelToken.source()
  // url
  let curReqIndex = JSON.stringify(params.url + params.method + JSON.stringify(params.params))
  // url+ = 
  if (localStorage.getItem(curReqIndex)) {
    console.log(':', localStorage)
    let pastData = JSON.parse(localStorage.getItem(curReqIndex)) // [{expiretime:,response:params}]
    let cacheExTime = [] // expiretime - 
    // (>0)(max)
    pastData.forEach(el => {
      cacheExTime.push(new Date(el.expiretime) - Date.now())
    })
    if (Math.max(...cacheExTime) > 0) {
      console.log('~')
      // cancelToken
      params.cancelToken = source.token
      // 
      backIndex = cacheExTime.indexOf(Math.max(...cacheExTime))
      backData = pastData[backIndex].response
      console.log('backData', backData)
      // source.cancel()error.message200
      source.cancel(JSON.stringify(backData))
    } else {
      console.log('~')
    }
  } else {
    console.log('~')
  }
  return params
}



